<template>
  <div id="app">
    <div class="app-phone">

      <div class="phone-header">
        <img src="./assets/ins.png" alt="">
      </div>

      <phone-body 
        :posts="posts"
        :step="step"
        :image="image"
      ></phone-body>

      <div class="phone-footer">

        <div class="home-cta">
          <i class="fas fa-home fa-lg"></i>
        </div>

        <div class="upload-cta">
          <input type="file" id="file" name="file" @change="uploadImage">
          <label for="file">
            <i class="far fa-plus-square fa-lg"></i>
          </label>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import phoneBody from "./components/phoneBody.vue"
import posts from "./data/posts.js"

export default {
  components:{
    phoneBody: phoneBody
  },
  data(){
    return{
      posts,
      step: 1,
      image: ""
    }
  },
  methods: {
    uploadImage(e){
      console.log(e.target.files[0].type);
      const files = e.target.files;
      if(!files.length) return;
      if(e.target.files[0].type == ('image/jpeg' || 'image/png' || 'image/gif')) {
        const reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onload = e => {
          this.image = e.target.result;
          this.step = 2;
        }
      }else{
        alert("请上传照片");
      }
    }
  }
}
</script>

<style lang="scss" src="./styles/app.scss"></style>
